import Home from './Pages/Home';
import './App.css';
import AboutUs from './Pages/AboutUs'
import SignUp from './Pages/SignUp'
import { Routes, Route } from 'react-router-dom'
import { createContext ,useState ,useEffect } from 'react'
import LogIn from './Pages/LogIn';
import Cart from './Pages/Cart';
import Error from './Pages/Error';


export const AppContext = createContext(null);


function App() {
   
  const getLocalData = () =>{
    const list  =localStorage.getItem("bazzar");
    if(list){
      return JSON.parse(list);
    }
    else{
      return [];
    }
  }
  
  const [loggedIn,setLoggedIn] = useState(false);

  const [data,setData] = useState(getLocalData());  
  const [currentUser,setCurrentUser] = useState("Default");
  const [products,setProducts] = useState(getLocalData());
  

  return (
    <AppContext.Provider value={{data,setData ,loggedIn,setLoggedIn,currentUser,setCurrentUser,products}}>
      <Routes>

        <Route path ="/" element={<Home />} />
        <Route path ="/AboutUs" element={<AboutUs />} />
        <Route path ="/SignUp" element={<SignUp />} />
        <Route path ="/LogIn" element={<LogIn />} />
        <Route path ="/Cart" element={<Cart />} />
        <Route path ="*" element={<Error />} />
      </Routes>
      
    </AppContext.Provider>
  );
}

export default App;
