import React, { useContext, useEffect, useState } from 'react'
import { useNavigate } from 'react-router'
import '../css/ProductList.css'
import { AppContext } from '../App';

const ProductList = () => {
  const {loggedIn} = useContext(AppContext);
  const {data} = useContext(AppContext);
  const {setData} = useContext(AppContext);
  const {currentUser} = useContext(AppContext);
  const navigate = useNavigate();
    const getLocalData = () =>{
      const list  =localStorage.getItem("fakestoreapi");
      if(list){
        return JSON.parse(list);
      }
      else{
        return [];
      }
    }
    const [products,setProducts] = useState(getLocalData());
    

    useEffect(()=>{
      try {
          const res =async () =>{
              const response = await fetch(`https://fakestoreapi.com/products`);
              const products = await response.json();
              console.log(products);
              setProducts(products);
          }
          res();
      }
      catch(err){
          console.log(err);
      }
      
    },[products])

    const AddToCart =(id)=>{
      console.log(id)
      // console.log(loggedIn)
        if(loggedIn){
          
          const found = data.filter((ele,id)=>{
            if(ele.Username===currentUser){
              return true;
            }
            return false;
          });
          const oldData = data.filter((ele,id)=>{
            if(ele.Username!==currentUser){
              return true;
            }
            return false;
          });
          if(found !==[]){
            // console.log(id);
            // console.log(oldData)
            // console.log(found[0]);
            const newobj = found[0];
            var oldcart = newobj.cart;
            
            // console.log(newobj.cart);
            
            if(oldcart.findIndex((ele)=>id===ele)===-1){
              console.log(newobj.cart)
              newobj.cart.push({id,qt:1});
              setData([...oldData,found[0]]);
              alert("Item added to the cart 😊")
            }
            else{
              alert("Item already exists 😉");
            }
            
          }
        }
        else{
          // navigate user to login option
          navigate('/LogIn')
        }
    }
  return (
    <div className='products-container'>
      {products[0]==="empty"?"Loading...":products.map((ele,id) =><div className="product" key ={id}>
        <div className='product-img-bg'>
        <img src={ele.image} alt="product" className="product-image" />
          </div>
          <div className="product-title">{ele.title}</div>
          <div className="product-description">{ele.description.substring(0,120)+"...."}</div>
          <div className="product-price">{"$" + ele.price}</div>
          <div className="product-rating">{"⭐ " + ele.rating.rate}</div>
          <button className='add-to-cart-btn' onClick={() => AddToCart(id)}>Add to Cart</button>
        </div>
      )
      }
    </div>
  )
}

export default ProductList
