import React ,{useContext, useState} from 'react'
import Footer from '../components/Footer'
import Navbar from '../components/Navbar'
import "../css/SignUp.css"
import { AppContext } from '../App'
import { useNavigate } from 'react-router-dom'

const LogIn = () => {
    const navigate = useNavigate();
    const { data } = useContext(AppContext);
    const { setLoggedIn } = useContext(AppContext);
    const { setCurrentUser } = useContext(AppContext); // sets the user id
    const [name,setName] = useState("");

    const [username,setUserName] = useState("");
    const [password,setPassword] = useState("");

    const unhash = (p) =>{
        let len = p.count;
        let s = p.password;
        let a = "",b="";
        for(let i = 0; i < len/2; i++){
            a+=s[i];
        }
        for(let i = len/2; i < len; i++){
            b+=s[i];
        }
        let ptra =0;
        let ptrb =0;
        let news ="";
        for(let i = 0; i < len; i++){
            if(i%2===0 && ptra<a.length){
                news+= a[ptra];
                ptra++;
            }
            else{
                news+=  b[ptrb++];
            }
        }
        return news;
    }

    const handleSubmit = (e) => {
        
        const isFound = data.some(element => {
            if (element.Username === username &&  unhash(element.Pass)=== password) {
                return true;
            }
            return false;
        }); 
        if(isFound){
            alert("Welcome 😉 " + username);
            setLoggedIn(true);
            setCurrentUser(username);
            // navigate user to login option
            navigate('/')
        }
        else{
            alert("Invalid username or password 😣 ");
        }
        setUserName("");
        setPassword("");
        e.preventDefault();
    };


    // handle changes
    const handleChangeUsername =(e)=>{
    setUserName(e.target.value);
    }
    const handleChangePassword =(e)=>{
    setPassword(e.target.value);
    }

  return (
    <>
      <Navbar />
      <form onSubmit={handleSubmit}>
        <p>UserName</p>
        <input type="text" value={username}  onChange ={handleChangeUsername} required/>
        <p>Password</p>
        <input type="password" value={password}  onChange ={handleChangePassword} required/>
        <button type="submit" className='signUp-btn'>Log In</button>
      </form>
      <Footer />
    </>
  )
}

export default LogIn
