import React ,{ useState ,useContext ,useEffect } from 'react'
import Navbar from '../components/Navbar';
import Footer from '../components/Footer';
import '../css/SignUp.css'
import { AppContext } from '../App'
import { Navigate, useNavigate } from 'react-router-dom';

const SignUp = () => {
    const navigate = useNavigate();
    const {data} = useContext(AppContext);
    const {setLoggedIn} = useContext(AppContext);
    const {setCurrentUser} = useContext(AppContext);

    const {setData} = useContext(AppContext);
    
    const [fullName,setFullName] = useState("");
    const [dob,setDob] = useState("");
    const [mobile,setMobile] = useState("");
    const [address,setAddress] = useState("");
    const [username,setUserName] = useState("");
    const [password,setPassword] = useState("");
    
    const hashpassword = (password) =>{
        let a = "";
        let b = "";
        for(let i = 0; i<password.length; i++){
            if(i%2===0)
            {
                a+=password[i];
            }
            else {
                b+=password[i];
            }
        }
        return { password:a+b , count:password.length};
    }

  const handleSubmit =(e) =>{
    
    const isFound = data.some(element => {
        if (element.Username === username) {
          return true;
        }
        return false;
      }); 
    
    if(isFound) {
        alert("username already exists ");
    }
    else{
        const newData = {Username:username,Pass:hashpassword(password),FullName:fullName,Dob:dob,Mobile:mobile,Address:address,cart:[]};
        // console.log(newData);
        setLoggedIn(true);
        setData([...data,newData]);
        setCurrentUser(username);
        alert("Welcome "+ username +" 😉 ");
        setUserName("");
        setPassword("");
        setFullName("");
        setMobile("");
        setAddress("");
        navigate('/');
    }
    
    e.preventDefault();
  }


  // adding localStorage
  useEffect(() => {
    localStorage.setItem("bazzar", JSON.stringify(data));
  }, [data]);

  // Handle the Changes 
  const handleChangeFullName =(e)=>{
    setFullName(e.target.value);
  }
  const handleChangeDob =(e)=>{
    setDob(e.target.value);
  }
  const handleChangeAddress =(e)=>{
    setAddress(e.target.value);
  }
  const handleChangeMobile =(e)=>{
    setMobile(e.target.value);
  }
  const handleChangeUsername =(e)=>{
    setUserName(e.target.value);
  }
  const handleChangePassword =(e)=>{
    setPassword(e.target.value);
  }

  return (
    <>
     <Navbar />
      <form onSubmit={handleSubmit}>
        <p>Full Name</p>
        <input type="text" value={fullName} onChange ={handleChangeFullName} required/> 
        <p>Date of Birth</p>
        <input type="date" value={dob}  onChange ={handleChangeDob} required/>
        <p>Moblie Number</p>
        <input type="tel" pattern="[0-9]{10}" value={mobile}  onChange ={handleChangeMobile} required/>
        <p>Address</p>
        <input type="text" value={address}  onChange ={handleChangeAddress} required/>
        <p>UserName</p>
        <input type="text" value={username}  onChange ={handleChangeUsername} required/>
        <p>Create New Password</p>
        <input type="password" value={password}  onChange ={handleChangePassword} required/>
        <button type="submit" className='signUp-btn'>SIGN UP</button>
      </form>
      <Footer />
    </>
  )
}

export default SignUp
