import React, { useContext } from "react";
import './Navbar.css'
import { Link } from 'react-router-dom'
import { AppContext } from "../App";

const Navbar = () => {
  const { loggedIn } = useContext(AppContext);
  const { setLoggedIn } = useContext(AppContext);
  return (
      <div className="navbar">
        <img src="./images/teknorix-logo.png" alt="teknorix-logo" className="nav-logo"/>
        <div className="nav-links">
          <Link to="/">Home</Link>
          <Link to="/AboutUs">About Us</Link>
          {loggedIn?"" :<><Link to="/Login">Login</Link> <Link to="/SignUp">SignUp</Link></>}
          {loggedIn?<><Link to='/Cart'>Cart</Link><Link onClick={()=>{setLoggedIn(false)}}>Log Out</Link></>:""}
          
        </div>
      </div>
  );
};
export default Navbar;
