import React,{useContext, useEffect, useState} from 'react'
import { AppContext } from '../App'
import Footer from '../components/Footer';
import Navbar from '../components/Navbar';
import '../css/Cart.css'

const Cart = () => {
    const [totalAmount,setTotalAmount] = useState(0);
    const getLocalData = () =>{
        const list  =localStorage.getItem("fakestoreapi");
        if(list){
            return JSON.parse(list);
        }
        else{
            return [];
        }
    }
    const { currentUser} = useContext(AppContext);
    const {data} = useContext(AppContext);
    const {loggedIn} = useContext(AppContext);
    const {setData} = useContext(AppContext);
    const [curCart,setCurCart] = useState([]);
    const [products,setProducts] = useState(getLocalData());
    
    const [countitem,setCountItem]  = useState([]);
    
    useEffect(()=>{
        
        const found = data.filter((ele,id)=>{
            if(ele.Username===currentUser){
              return true;
            }
            return false;
          });
        const newobj = found[0];
        console.log(newobj.cart);
        setCurCart(newobj.cart);
        var tot = 0;
        const a = curCart.map((ele)=>{
            // console.log(products[ele]);
            tot+=parseInt(products[ele.id].price*ele.qt);
        });
        setTotalAmount(tot);
        if(loggedIn===false){
            setCurCart([]);
        }
    },[curCart,data,loggedIn])
    // Remove item from cart
    const RemoveItem =(id)=>{
        const oldcart = curCart.filter((ele,ind)=>{
            if(ele.id!== id){
                return true;
            }
            return false;
        });
        console.log(oldcart)
        const found = data.filter((ele,id)=>{
            if(ele.Username===currentUser){
              return true;
            }
            return false;
        });
        const oldData = data.filter((ele,id)=>{
            if(ele.Username!==currentUser){
              return true;
            }
            return false;
        });
        if(found !==[]){
            console.log(id);
            console.log(oldData)
            console.log(found[0]);
            const newobj = found[0];
            console.log(newobj.cart)
            newobj.cart = oldcart;
            console.log(found[0].cart)
            setData([...oldData,found[0]]);
        }



    }
    const loadScript =(src) =>{
        return new Promise((resolve) =>{
            const script1 =document.createElement('script')
            script1.src =src;
            
            script1.onload = () =>{
                resolve(true);
            }
            script1.onerror = () =>{
                resolve(false);
            }

            document.body.appendChild(script1);
        })
    }
    const handleCheckout = async ()=>{
        // alert(totalAmount)
        const found = data.filter((ele,id)=>{
            if(ele.Username===currentUser){
              return true;
            }
            return false;
        });

        const res = await loadScript('https://checkout.razorpay.com/v1/checkout.js');
        if(!res)
        {
            alert("You are offline my Friend not able to connect to server.");
            return;
        }
        

        const options = {
            key: "rzp_test_zGE4jZg0ieUO9d",
            currency: "USD",
            amount: totalAmount*100,
            name: "Teknorix",
            description: "Thanks for shopping with us !",
            image: "images/teknorix-logo.png",
            handler: function(response){
        
                alert("Payment Success! and your order id is " + response.razorpay_payment_id);
            },
            prefill:{
                "name": found[0].FullName,
                "email": found[0].Username,
                "contact": found[0].Mobile
            }
        };
        
        const paymentObj = new window.Razorpay(options)
        paymentObj.open()

        
        
    }

    const IncreaseQt = (pr_id) =>{
        console.log(pr_id)
        let temp = curCart.filter((ele)=>{
            if(ele.id === pr_id){
                console.log(ele);
                return true;
            }
            return false;
        })
        console.log(temp)
        let remain = curCart.filter((ele)=>{
            if(ele.id !== pr_id){

                return true;
            }
            return false;
        })
        console.log(remain)
        temp[0].qt++;
        setCurCart([...remain,...temp]);
    }
    const DecreaseQt = (pr_id) =>{
        console.log(pr_id)

        let temp = curCart.filter((ele)=>{
            if(ele.id === pr_id){
                console.log(ele)
                return true;
            }
            return false;
        })
        console.log(temp)
        let remain = curCart.filter((ele)=>{
            if(ele.id !== pr_id){
                return true;
            }
            return false;
        })
        console.log(remain)
        
        if(temp[0].qt>1){
            temp[0].qt--;
            console.log(temp)
        }
        setCurCart([...remain,...temp]);
    }



    return (
        <>
        <Navbar />
    <div className='cart-container'>
        <div className='leftpart'>
        {curCart.map((ele,id)=> <div className="cart-items" key={id}> 
            <div className='product-img-bg'>
            <img src={products[ele.id].image} alt="product" className="product-image" />
              </div>
              <div className="product-title">{products[ele.id].title}</div>
              <div className="product-price">{"$" + (products[ele.id].price*ele.qt).toFixed(2)}</div>
              <div className="button-group">
                <button className='add-to-cart-btn' onClick={() => RemoveItem(ele.id)}>Remove Item</button>
                <button className='item-count-inc' onClick={() => IncreaseQt(ele.id)}>+</button>
                <span className="item-count">{ele.qt}</span>
                <button className='item-count-dec' onClick={() => DecreaseQt(ele.id)}>-</button>
              </div>
            </div>
        )}
        </div>
        <div className="checkout">
            <div className="price">
              TOTAL Price : {"$ "+totalAmount}
            </div>
            <div className="DC">
                Delivery charges FREE !!!
            </div>
            <div className="price">
                TOTAL charges : {"$ "+totalAmount}
            </div>
            <button className='checkout-btn' onClick={handleCheckout}>Checkout</button>
        </div> 
    </div>
    <Footer />
    </>
  )
}

export default Cart
